$(function () {
  var $videoContainer = $('#video'),
    $videoControls = $('.video-control'),
    $video = $('#myVideo')[0];

  $videoControls.click(function () {
    if ($video.paused) {
      $video.play();
      $videoContainer.addClass('video-is-playing');
    } else {
      $video.pause();
      $videoContainer.removeClass('video-is-playing');
      //  возвращаем постер
      $video.load();
    }
  });
});


const cabinetHeaderBtn = document.querySelector(".cabinet__header-btn");
const headerSidebar = document.querySelector(".header__sidebar ");
const headerCabinet = document.querySelector(".header-cabinet__wrapper");
const arrowLeft = document.querySelector('.arrow__left');
const mainContent = document.querySelectorAll('.main__content');


cabinetHeaderBtn.addEventListener('click', function(){
  headerSidebar.classList.toggle('hiddden');
  headerCabinet.classList.toggle('header__hidden');
  arrowLeft.classList.toggle('arrow__rotate');
  

  mainContent.forEach(element => {
    element.classList.toggle('header__hidden');
  });
 
})


const menuSideBar = document.querySelectorAll('.main__menu-item');

menuSideBar.forEach(e => {
  e.addEventListener('click', function(){
    menuSideBar.forEach(item => {
      item.classList.remove('active');
    })
    e.classList.add('active');
  })
    
  })


const tabsParners = document.querySelectorAll('.btn--partners__tabs');


tabsParners.forEach(e => {
  e.addEventListener('click', function(){
    tabsParners.forEach(item => {
      item.classList.remove('partners__active');
    })
    e.classList.add('partners__active');
  })
})

const burger = document.querySelector('.burger');
const closeBurger = document.querySelector('.mobile__sidebar-btn--close');
const mobileSideBar = document.querySelector('.mobile__sidebar');
const body = document.querySelectorAll('.body');


burger.addEventListener('click', function(){
  mobileSideBar.classList.add('mobile__sidebar--active');
  body.forEach(e => {
    e.classList.add('body-lock');
  })
  
  

})


mobileSideBar.addEventListener('click', function(){
  mobileSideBar.classList.remove('mobile__sidebar--active');
  body.forEach(e => {
    e.classList.remove('body-lock');
  })
})

const text = document.getElementById('user__link-copy');
const btn = document.getElementById('copy__button');





/* вызываем функцию при нажатии на кнопку */
btn.addEventListener('click', function(){
  text.select();    
  document.execCommand("copy");
});
